*, *::after, *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  width: 100vw;
  max-width: 100vw;
  min-height: 100vh; }

header, main, footer {
  width: inherit;
  max-width: inherit;
  max-height: inherit; }

ul {
  list-style: none; }

.container ul {
  align-content: center;
  display: grid;
  grid-template-columns: repeat(4, 100px);
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  min-height: 100vh; }

.residence {
  display: flex;
  justify-content: center;
  align-items: center; }
  .residence span {
    font-size: 1.67rem; }

.house {
  background: -webkit-gradient(linear, left bottom, right top, from(#d32f2f), to(#e57373));
  background: -webkit-linear-gradient(bottom left, #d32f2f, #e57373);
  background: linear-gradient(to top right, #d32f2f, #e57373);
  width: 100px;
  height: 100px; }

.yellow {
  background: -webkit-gradient(linear, left bottom, right top, from(#fbc02d), to(#fff176));
  background: -webkit-linear-gradient(bottom left, #fbc02d, #fff176);
  background: linear-gradient(to top right, #fbc02d, #fff176); }

.green {
  background: -webkit-gradient(linear, left bottom, right top, from(#00796b), to(#4db6ac));
  background: -webkit-linear-gradient(bottom left, #00796b, #4db6ac);
  background: linear-gradient(to top right, #00796b, #4db6ac); }

.red {
  background: -webkit-gradient(linear, left bottom, right top, from(#d32f2f), to(#e57373));
  background: -webkit-linear-gradient(bottom left, #d32f2f, #e57373);
  background: linear-gradient(to top right, #d32f2f, #e57373); }

